"use client";

import { useEffect, useState } from "react";

export default function ReelsFeed() {
  const [reels, setReels] = useState([]);

  const fetchReels = async () => {
    try {
      const res = await fetch("/api/reels");
      const data = await res.json();
      setReels(data.reels || []);
    } catch (err) {
      console.error("Fetch reels error:", err);
    }
  };

  useEffect(() => {
    fetchReels();
  }, []);

  return (
    <div>
      <h2>Reels</h2>

      {reels.length === 0 ? (
        <p>Loading reels...</p>
      ) : (
        <ul style={{ listStyle: "none", padding: 0 }}>
          {reels.map(reel => (
            <li key={reel.id} style={{ marginBottom: "20px" }}>
              <strong>{reel.author?.name}</strong>
              <video 
                controls 
                style={{ width: "100%", borderRadius: "10px", marginTop: 10 }}
              >
                <source src={reel.video} />
              </video>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
